import { createRouter, createWebHistory } from 'vue-router';
import { useAuthStore } from "@/stores/useAuth";

import HomeView from "@/views/HomeView.vue";
import DockyardView from "@/views/DockyardView.vue";
import DockyardsView from "@/views/DockyardsView.vue";
import UserView from "@/views/UserView";
import PlanningView from "@/views/PlanningView";
import FeesView from "@/views/FeesView";
import FeeView from "@/views/FeeView";
import FeeCreateView from "@/views/Forms/FeeCreateView";
import LoginView from "@/views/LoginView";
import NotFoundView from "@/views/NotFoundView";
import HomeAdminView from "@/views/Admin/HomeAdminView";
import DockyardCreate from "@/views/Admin/Forms/DockyardCreate";

const redirectToHomeOnLoggedIn = (to, from, next) => {
  if (useAuthStore().loggedIn) next({ name: "Accueil" });
  else next();
};


const routes = [
  {
    path: '/',
    name: 'Accueil',
    component: HomeView,
    meta: { layout: "loggedIn-layout" }

  },
  {
    path: '/chantier',
    name: 'Chaniters',
    component: DockyardsView,
    meta: { layout: "loggedIn-layout" }
  },
  {
    path: '/chantier/:id',
    name: 'Chaniter',
    component: DockyardView,
    meta: { layout: "loggedIn-layout" }
  },
  {
    path: '/planning',
    name: 'Planning',
    component: PlanningView,
    meta: { layout: "loggedIn-layout" }
  },
  {
    path: '/utilisateur',
    name: 'Compte',
    component: UserView,
    meta: { layout: "loggedIn-layout" }
  },
  {
    path: '/frais',
    name: 'Frais',
    component: FeesView,
    meta: { layout: "loggedIn-layout" }
  },
  {
    path: '/frais/:id',
    name: 'Consultation frais',
    component: FeeView,
    meta: { layout: "loggedIn-layout" }
  },
  {
    path: '/frais/creation',
    name: 'Création de frais',
    component: FeeCreateView,
    meta: { layout: "loggedIn-layout" }
  },



  {
    path: '/admin',
    name: 'Administration',
    component: HomeAdminView,
    meta: { layout: "loggedIn-layout", role: "admin" }
  },
  {
    path: '/chantier/creation',
    name: 'Création de chantier',
    component: DockyardCreate,
    meta: { layout: "loggedIn-layout", role: "admin" }
  },








  {
    path: '/login',
    name: 'Connexion',
    component: LoginView,
    beforeEnter: redirectToHomeOnLoggedIn,
  },
  {
    path: "/:pathMatch(.*)*",
    component: NotFoundView,
  },


]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth && !useAuthStore().loggedIn)
    next({ name: "accounts-login" });
  else next();
});

export default router
