<template>
  <h1 class="text-3xl font-bold pb-10">Création Chantier</h1>
  <div class="form grid md:grid-cols-3 gap-5">

    <div class="card bg-base-100 shadow-md h-min">
      <ul id="steplist" class="card-body steps md:steps-vertical steps-horizontal w-full">
        <li class="step step-primary">Chantier</li>
        <li class="step">Membres</li>
        <li class="step">Utiles</li>
        <li class="step">Pratiques</li>
        <li class="step">Récap</li>
      </ul>
    </div>

    <div class="card bg-base-100 shadow-md w-full md:col-span-2">
      <div class="card-body">
        <DockyardStep1 v-if="step === 0"></DockyardStep1>
        <DockyardStep2 v-if="step === 1"></DockyardStep2>
        <DockyardStep3 v-if="step === 2"></DockyardStep3>
        <DockyardStep4 v-if="step === 3"></DockyardStep4>
        <DockyardStep5 v-if="step === 4"></DockyardStep5>


        <div class="flex justify-around pt-5">
          <button @click="changeStepList(-1)" class="btn" v-if="step > 0">Précédent</button>
          <button @click="changeStepList(+1)" class="btn btn-secondary" v-if="step < 4">Suivant</button>
          <button type="submit" class="btn btn-primary" v-else>Créer</button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import DockyardStep1 from "@/components/Forms/Stepper/Dockyard/DockyardStep1";
import DockyardStep2 from "@/components/Forms/Stepper/Dockyard/DockyardStep2";
import DockyardStep3 from "@/components/Forms/Stepper/Dockyard/DockyardStep3";
import DockyardStep4 from "@/components/Forms/Stepper/Dockyard/DockyardStep4";
import DockyardStep5 from "@/components/Forms/Stepper/Dockyard/DockyardStep5";


export default {
  name: "DockyardCreate",
  components: {DockyardStep5, DockyardStep4, DockyardStep3, DockyardStep2, DockyardStep1},
  methods: {
    changeStepList(n){
      if (this.step >= 0 && this.step < this.steplist.length){
        n === +1 ? this.steplist[this.step + 1].classList.add("step-primary") : this.steplist[this.step].classList.remove("step-primary")
        this.step += n;
      }
    }
  },
  data(){
    let step = 0;
    const steplist = [];
    return{
      step,
      steplist
    }
  },
  mounted() {
    this.step = 0;
    this.steplist = document.getElementById("steplist").getElementsByTagName('li');
  }
}
</script>

