<template>
  <h1 class="text-2xl pb-6">Ajouter Membres</h1>

  <div class="form-control w-full pb-3">
    <input v-model="searchText" type="text" placeholder="Rechercher un utilisateur . . ." class="input input-bordered w-full"/>
  </div>


  <div class="overflow-x-auto w-full scrollbar-primary" style="max-height: 350px">
    <table class="table w-full table-compact bordered">
      <thead>
      <tr>
        <th>
          <label>
            <input type="checkbox" disabled class="checkbox"/>
          </label>
        </th>
        <th>Nom</th>
        <th>Role</th>
        <th>Action</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="user in searchResult()" v-bind:key="user.id">
        <th>
          <label>
            <input type="checkbox" class="checkbox"/>
          </label>
        </th>
        <td>
          <div class="flex items-center space-x-3">
            <div>
              <div class="font-bold">{{ user.username }}</div>
            </div>
          </div>
        </td>
        <td>
          <span class="badge badge-primary badge-md">{{ user.role }}</span>
        </td>
        <th>
          <button class="btn btn-ghost btn-xs">details</button>
        </th>
      </tr>

      <div class="item error" v-if="!searchResult().length">
        <p>No results found!</p>
      </div>
      </tbody>
    </table>
  </div>

  <div class="flex justify-end">
    <button class="btn btn-outline btn-xs">Ajouter</button>
  </div>

  <div class="divider"></div>

</template>

<script>
export default {
  name: "DockyardStep2",
  data() {
    const users = [
      {id: 1, username: "Lucas Reteau", role: "SAV"},
      {id: 2, username: "Tom Poyvre", role: "Pisciniste"},
      {id: 3, username: "Mathéo Épie", role: "Technicien"},
    ];
    let siteManager = {};
    let dockyardMembers = [];
    let searchText = '';

    return {
      searchText,
      dockyardMembers,
      users,
      siteManager
    }
  },

  methods: {
    searchResult(){
      if(this.searchText){
        return this.users.filter((user)=>{
          return this.searchText.toLowerCase().split(' ').every(v => user.username.toLowerCase().includes(v) || user.role.toLowerCase().includes(v))
        })
      }else{
        return this.users;
      }
    }
    
  }
}
</script>

